#page{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
#load{
    animation-name: loading;
    animation-iteration-count: infinite;
    animation-duration: 2s;
    
}

@keyframes loading {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

.list{
    margin: 0px auto;
    width: 90%;
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(300px,1fr)); 
}
.card{
    margin: 15px;
    transition: all 1s;
}
.card:hover{
    transform: scale(1.1);
}